<template>
    <div>
        <b-container class="bv-example-row text-left">
        <b-input-group :prepend="name" class="mt-3 mb-3">
            <b-form-textarea id="textarea" v-model="context" :placeholder="isSubComment ? ' .' : ' .'" rows="3" max-rows="6">
            </b-form-textarea>

            <b-input-group-append>
                <b-button variant="info" @click="isSubComment ? createSubComment() : createComment()">{{isSubComment ? '' : ''}}</b-button>
            </b-input-group-append>
        </b-input-group>
        </b-container>
    </div>
</template>

<script>
import data from "@/data";

export default {
    name: 'CommentCreate',
    props:{
        contentId:Number,
        commentId:Number,
        isSubComment: Boolean,
        reloadComment:Function,        
        subCommentToggle:Function,
        reloadSubComments:Function
    },
    data(){
        return{
            name: '',
            context:''
        };        
    },
    methods: {
        createComment(){ 
            data.Comment.push({
            comment_id : data.Comment[data.Comment.length -1].comment_id + 1,
            user_id: 1,     //
            content_id : this.contentId,
            context:this.context,
            created_at: this.$moment(new Date()).format('YYYY-MM-DD hh:mm'),
            update_at:null
            });

            //       >   
            this.reloadComment();

            //  context 
            this.context="";
        },
        createSubComment(){ 
            data.SubComment.push({
            subcomment_id : data.SubComment[data.SubComment.length -1].subcomment_id + 1,
            user_id: 1,     //
            comment_id : this.commentId,
            context:this.context,
            created_at:this.$moment(new Date()).format('YYYY-MM-DD hh:mm'),
            update_at:null
            });

            this.subCommentToggle();
            this.reloadSubComments();
            //  context 
            this.context="";
        }
    }
}
</script>