<template>
    <div class="text-left">   
    <b-table-simple>
            <tr>
                <th></th>
                <td>{{data.writer}}</td>
                <th></th>
                <td>{{data.title}}</td>
            </tr>
            

            <tr>
                <th colspan="1"></th>
                <td colspan="3">{{data.content}}</td>
            </tr>

    </b-table-simple>
    
        <div class="text-center"> 
            <b-button variant="outline-success " @click="updateData" ></b-button>
            <b-button variant="outline-danger" @click="deleteData"></b-button>
        </div>

    </div>
</template>

<script>
import data from '@/data';

export default {
    name:'Detail',
    data(){
        const index = this.$route.params.contentId
        return{
            data:data.Read[index],
            index:index,
        }
    },
    methods:{
        deleteData(){
           data.Read.splice(this.index, 1), //     

            this.$router.push({
               path:'/read'

           })

        },
        updateData(){
            this.$router.push({
                name:'Create',
                params:{
                    contentId: this.index
                }
            })
        }
    }
}
</script>