<template>
    <div class="text-left">
        <b-table-simple>
             <!--     v-modle-->
            <tr>
                <th></th>
                <td><input v-model="writer" placeholder=""/></td>
                <th></th>
                <td><input v-model="title" placeholder=""/></td>
            </tr>
            <tr>
                <th colspan="1"></th>
                <td colspan="3"> <textarea v-model="content" placeholder=""/></td>
            </tr>            

            
        </b-table-simple>
        <b-button variant="outline-primary" @click="index !== undefined ? update() : write()">{{index !== undefined ? '' : ''}}</b-button>
    </div>
</template>

<script>
import data from '@/data';

export default {
    name:'Create',
    data(){
        const index = this.$route.params.contentId;
        return{
            data: data,
            index: index,
            writer: index !== undefined ? data.Read[index].writer : "",  //     
            title: index !== undefined ? data.Read[index].title : "",
            content: index !== undefined ? data.Read[index].content : ""
        }
    },
    methods: {
        write(){
            //   .
            this.data.Read.push({
                writer:this.writer,
                title:this.title,
                content:this.content
            }),

            this.$router.push({
                path:'/read'
            })
        },

        update(){
            data.Read[this.index].writer = this.writer,
            data.Read[this.index].title = this.title,
            data.Read[this.index].content = this.content,
            this.$router.push({
                path:'/read'
            })

        }

    }
    
}
</script>